import { Icon } from '@cetus/ui-kit'
import { Box, HStack, Skeleton, Text } from '@chakra-ui/react'

type FunnelPriceProps = {
  label?: string
  price?: string
  perText?: string
  showIcon?: boolean
}

function FunnelPrice({ label = 'Current Price', price, perText, showIcon = true }: FunnelPriceProps) {
  return (
    <HStack gap="4px">
      {showIcon && (
        <Box p="2px" borderRadius="4px" bg="white_color_opacity.10" mr="4px">
          <Icon xlinkHref="#icon-funnel" svgFill="text_caption" fontSize="16px" />
        </Box>
      )}

      <Text fontSize="14px">{label}</Text>
      {price && (
        <>
          <Text color="text_caption" fontSize="14px">
            {price}
          </Text>
          <Text fontSize="14px">{perText}</Text>
        </>
      )}
      {!price && <Skeleton w="66px" h="14px" display="inline-block" />}
    </HStack>
  )
}

export default FunnelPrice
